
.pure-button {
  transition: all 0.3s ease;
  border-radius: 4px;
  background: none;
  background-color: transparent;
  border: #29b474 1px solid;
  color: #29b474;
}
.pure-button:hover {
  background: none;
  background-color: #29b474;
  color: #fff;
}
.pure-button.full-width {
  display: block;
  margin-bottom: 10px;
}
.pure-button.pure-button-primary {
  background-color: #29b474;
  color: #fff;
}
.pure-button.pure-button-primary:hover {
  background-color: #25a268;
}
.pure-button.pure-button-success {
  background-color: #29b474;
  color: #fff;
}
.pure-button.pure-button-success:hover {
  background-color: #25a268;
}
.pure-button.pure-button-subtle {
  background-color: transparent;
  border-color: #ccc;
  color: #666;
}
.pure-button.pure-button-subtle:hover {
  background-color: #e1e1e1;
  color: #444;
}
.pure-button.pure-button-text {
  border-color: transparent;
}
.pure-button.pure-button-homework {
  background-color: transparent;
  border-color: #fd3c51;
  color: #fd3c51;
}
.pure-button.pure-button-homework:hover {
  background-color: #fd3c51;
  color: #fff;
}
@import url("https://fonts.googleapis.com/css?family=Arimo");
@import url("https://fonts.googleapis.com/css?family=Source+Sans+Pro");
html,
body {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.pull-left {
  float: left;
}
.pull-right {
  float: right;
}
.clearfix {
  clear: both;
  float: none;
}
.fa-icon {
  width: auto;
  height: 1em;
}
.no-margin {
  margin: 0 !important;
}
.no-padding {
  padding: 0 !important;
}
.background-white {
  background-color: #fff !important;
}
.text-white {
  color: #fff;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.2s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.fade-enter-to,
.fade-leave {
  opacity: 1;
}
.main-container {
  border-radius: 4px;
  position: relative;
}
.main-container.main-container-padded {
  padding: 20px;
}
.content-block {
  border-radius: 4px;
  padding: 20px;
  margin: 20px 0 0 0;
}
.content-block.white-block {
  background-color: #fff;
}
.icon-margin {
  margin: 0 5px;
}
.fourcorners-submission {
  border-radius: 6px;
  background-color: #999;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  overflow: hidden;
  padding: 15px;
  width: 100%;
}
.fourcorners-submission.fourcorners-submission-submit label {
  color: #fff;
}
.fourcorners-submission.fourcorners-submission-submit p {
  margin: 0;
  padding: 0;
  color: #fff;
}
.fourcorners-submission.fourcorners-submission-submit input[type="text"] {
  border-radius: 6px;
  border: none;
  box-shadow: 0 0 10px 5px rgba(0,0,0,0.01);
  -moz-box-shadow: 0 0 10px 5px rgba(0,0,0,0.01);
  -webkit-box-shadow: 0 0 10px 5px rgba(0,0,0,0.01);
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  line-height: 40px;
  margin: 10px 0;
  padding: 0 10px;
  outline: 0;
  resize: none;
  width: 100%;
}
.fourcorners-submission.fourcorners-submission-submit .pure-button {
  background-color: transparent;
  border: #fff 1px solid;
  color: #fff;
  margin-top: 10px;
}
.fourcorners-submission.fourcorners-submission-submit .pure-button:hover {
  background-color: #fff;
  border-color: #fff;
  color: #161616;
}
.fourcorners-submission.fourcorners-submission-submitted,
.fourcorners-submission.fourcorners-submission-submitting,
.fourcorners-submission.fourcorners-submission-checking {
  text-align: center;
}
.fourcorners-submission.fourcorners-submission-submitted h2,
.fourcorners-submission.fourcorners-submission-submitting h2,
.fourcorners-submission.fourcorners-submission-checking h2 {
  margin: 0;
  padding: 0;
  color: #fff !important;
  padding: 40px;
}
.fourcorners-submission.fourcorners-submission-submitting,
.fourcorners-submission.fourcorners-submission-checking {
  background-color: #e1e1e1;
}
.fourcorners-submission.fourcorners-submission-submitting h2,
.fourcorners-submission.fourcorners-submission-checking h2 {
  color: #444 !important;
}
.fourcorners-submission.fourcorners-submission-submitted {
  background-color: #29b474;
}


