
.pure-button {
  transition: all 0.3s ease;
  border-radius: 4px;
  background: none;
  background-color: transparent;
  border: #29b474 1px solid;
  color: #29b474;
}
.pure-button:hover {
  background: none;
  background-color: #29b474;
  color: #fff;
}
.pure-button.full-width {
  display: block;
  margin-bottom: 10px;
}
.pure-button.pure-button-primary {
  background-color: #29b474;
  color: #fff;
}
.pure-button.pure-button-primary:hover {
  background-color: #25a268;
}
.pure-button.pure-button-success {
  background-color: #29b474;
  color: #fff;
}
.pure-button.pure-button-success:hover {
  background-color: #25a268;
}
.pure-button.pure-button-subtle {
  background-color: transparent;
  border-color: #ccc;
  color: #666;
}
.pure-button.pure-button-subtle:hover {
  background-color: #e1e1e1;
  color: #444;
}
.pure-button.pure-button-text {
  border-color: transparent;
}
.pure-button.pure-button-homework {
  background-color: transparent;
  border-color: #fd3c51;
  color: #fd3c51;
}
.pure-button.pure-button-homework:hover {
  background-color: #fd3c51;
  color: #fff;
}
@import url("https://fonts.googleapis.com/css?family=Arimo");
@import url("https://fonts.googleapis.com/css?family=Source+Sans+Pro");
html,
body {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.pull-left {
  float: left;
}
.pull-right {
  float: right;
}
.clearfix {
  clear: both;
  float: none;
}
.fa-icon {
  width: auto;
  height: 1em;
}
.no-margin {
  margin: 0 !important;
}
.no-padding {
  padding: 0 !important;
}
.background-white {
  background-color: #fff !important;
}
.text-white {
  color: #fff;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.2s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.fade-enter-to,
.fade-leave {
  opacity: 1;
}
.main-container {
  border-radius: 4px;
  position: relative;
}
.main-container.main-container-padded {
  padding: 20px;
}
.content-block {
  border-radius: 4px;
  padding: 20px;
  margin: 20px 0 0 0;
}
.content-block.white-block {
  background-color: #fff;
}
.icon-margin {
  margin: 0 5px;
}
.time-segment {
  background-color: #fff;
  height: 157px;
  min-height: 157px;
  overflow: hidden;
  position: absolute;
  left: 50%;
  margin-left: -390px;
  z-index: 0;
  width: 780px;
}
@media (max-width: 800px) {
.time-segment {
    left: 10px;
    margin-left: 0;
    width: calc(100% - 20px);
}
}
.time-segment .primary-wrapper {
  background-color: #fff;
  border-bottom: #e9e9e9 1px solid;
  height: 156px;
  min-height: 156px;
  position: relative;
  z-index: 2;
}
.time-segment .primary-wrapper .segment-label--group {
  border-radius: 4px;
  transition: all 0.3s ease;
  background: #f9f9f9;
  font-size: 0.8em;
  opacity: 0.05;
  padding: 6px 12px;
  position: absolute;
  top: 10px;
  left: 10px;
  z-index: 2;
}
.time-segment .primary-wrapper .suggestion {
  padding: 20px;
  text-align: center;
}
.time-segment .primary-wrapper .suggestion h3 {
  margin: 0;
  padding: 0;
  color: #444;
}
.time-segment .primary-wrapper .message-wrapper {
  transition: all 0.3s ease;
  position: absolute;
  top: 50%;
}
.time-segment .primary-wrapper .subtitle-wrapper {
  transition: all 0.3s ease;
  position: absolute;
  top: 50%;
}
.time-segment .primary-wrapper:hover {
  cursor: pointer;
}
.time-segment .primary-wrapper:hover .segment-label--group {
  opacity: 1;
}
.time-segment.current .primary-wrapper:after {
  border-radius: 50%;
  background-color: #29b474;
  content: '';
  position: absolute;
  top: 10px;
  left: 10px;
  height: 12px;
  width: 12px;
}
.time-segment.peek,
.time-segment.active {
  border-radius: 4px;
  z-index: 56;
  border: none;
}
.time-segment.peek {
  height: 240px;
}
.time-segment.opened {
  height: auto;
}
.time-segment.opened .segment-expansion-bar {
  opacity: 0;
  pointer-events: none;
}
.time-segment .meta-container {
  transition: all 0.3s ease;
  background-color: #f9f9f9;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  opacity: 0;
  padding: 10px;
  position: absolute;
  top: 157px;
  bottom: 50px;
  right: 0;
  left: 0;
  overflow: scroll;
}
.time-segment .meta-container .message-wrapper {
  -webkit-transform: translate(0, 0) !important;
  transform: translate(0, 0) !important;
  width: 100%;
}
.time-segment .meta-container .message-wrapper.featured {
  border-left: #29b474 2px solid;
}
.time-segment .meta-container .message-wrapper.featured:before {
  content: '';
  height: 100px;
  width: 100px;
}
.time-segment .meta-container .message-wrapper .tweet-actions {
  background-color: inherit;
}
.time-segment .meta-container.active {
  opacity: 1;
}
.quick-note {
  border-top: #e9e9e9 1px solid;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 0;
}
.segment-expansion-bar {
  transition: all 0.3s ease;
  background-color: #fff;
  color: #999;
  cursor: pointer;
  padding: 5px 20px;
  position: absolute;
  bottom: 51px;
  left: 0;
  right: 0;
  z-index: 1;
  text-align: center;
}
.segment-expansion-bar:hover {
  background-color: #f9f9f9;
}


