
.pure-button {
  transition: all 0.3s ease;
  border-radius: 4px;
  background: none;
  background-color: transparent;
  border: #29b474 1px solid;
  color: #29b474;
}
.pure-button:hover {
  background: none;
  background-color: #29b474;
  color: #fff;
}
.pure-button.full-width {
  display: block;
  margin-bottom: 10px;
}
.pure-button.pure-button-primary {
  background-color: #29b474;
  color: #fff;
}
.pure-button.pure-button-primary:hover {
  background-color: #25a268;
}
.pure-button.pure-button-success {
  background-color: #29b474;
  color: #fff;
}
.pure-button.pure-button-success:hover {
  background-color: #25a268;
}
.pure-button.pure-button-subtle {
  background-color: transparent;
  border-color: #ccc;
  color: #666;
}
.pure-button.pure-button-subtle:hover {
  background-color: #e1e1e1;
  color: #444;
}
.pure-button.pure-button-text {
  border-color: transparent;
}
.pure-button.pure-button-homework {
  background-color: transparent;
  border-color: #fd3c51;
  color: #fd3c51;
}
.pure-button.pure-button-homework:hover {
  background-color: #fd3c51;
  color: #fff;
}
@import url("https://fonts.googleapis.com/css?family=Arimo");
@import url("https://fonts.googleapis.com/css?family=Source+Sans+Pro");
html,
body {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.pull-left {
  float: left;
}
.pull-right {
  float: right;
}
.clearfix {
  clear: both;
  float: none;
}
.fa-icon {
  width: auto;
  height: 1em;
}
.no-margin {
  margin: 0 !important;
}
.no-padding {
  padding: 0 !important;
}
.background-white {
  background-color: #fff !important;
}
.text-white {
  color: #fff;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.2s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.fade-enter-to,
.fade-leave {
  opacity: 1;
}
.main-container {
  border-radius: 4px;
  position: relative;
}
.main-container.main-container-padded {
  padding: 20px;
}
.content-block {
  border-radius: 4px;
  padding: 20px;
  margin: 20px 0 0 0;
}
.content-block.white-block {
  background-color: #fff;
}
.icon-margin {
  margin: 0 5px;
}
.conversation-container {
  background-color: #fff;
  position: relative;
}
.conversation-container.collapsed {
  max-height: 600px;
  overflow: hidden;
}
.conversation-container h5 {
  margin: 0;
  padding: 0;
  color: #444;
  height: 60px;
  line-height: 60px;
  width: 100%;
}
.conversation-container #activity-visualisation {
  pointer-events: none;
  position: absolute;
  right: 0;
  top: 0;
  z-index: 0;
}
.conversation-container #activity-visualisation svg {
  overflow: visible;
}
.conversation-container #activity-visualisation svg path {
  fill: #29b474;
}
@media (max-width: 600px) {
.conversation-container #activity-visualisation {
    z-index: 50;
    left: -400px;
    right: auto;
}
.conversation-container #activity-visualisation svg path {
    fill: rgba(41,180,116,0.3);
}
}
.conversation-container .inner-wrapper {
  overflow: hidden;
}
.conversation-container .inner-wrapper .subtitle-wrapper,
.conversation-container .inner-wrapper .message-wrapper {
  transition: all 0.3s ease;
  width: 50%;
}
.conversation-container .inner-wrapper .subtitle-wrapper.subtitle-wrapper,
.conversation-container .inner-wrapper .message-wrapper.subtitle-wrapper {
  -webkit-transform: translate(0%, -50%);
  transform: translate(0%, -50%);
}
.conversation-container .inner-wrapper .subtitle-wrapper.message-wrapper,
.conversation-container .inner-wrapper .message-wrapper.message-wrapper {
  -webkit-transform: translate(100%, -50%);
  transform: translate(100%, -50%);
}
@media (max-width: 600px) {
.conversation-container .inner-wrapper .subtitle-wrapper,
  .conversation-container .inner-wrapper .message-wrapper {
    width: 100%;
}
.conversation-container .inner-wrapper .subtitle-wrapper.subtitle-wrapper,
  .conversation-container .inner-wrapper .message-wrapper.subtitle-wrapper {
    -webkit-transform: translate(0%, -50%);
    transform: translate(0%, -50%);
}
.conversation-container .inner-wrapper .subtitle-wrapper.message-wrapper,
  .conversation-container .inner-wrapper .message-wrapper.message-wrapper {
    -webkit-transform: translate(100%, -50%);
    transform: translate(100%, -50%);
}
.conversation-container .inner-wrapper.message-priority .subtitle-wrapper,
  .conversation-container .inner-wrapper.message-priority .message-wrapper {
    width: 100%;
}
.conversation-container .inner-wrapper.message-priority .subtitle-wrapper.subtitle-wrapper,
  .conversation-container .inner-wrapper.message-priority .message-wrapper.subtitle-wrapper {
    -webkit-transform: translate(-100%, -50%);
    transform: translate(-100%, -50%);
}
.conversation-container .inner-wrapper.message-priority .subtitle-wrapper.message-wrapper,
  .conversation-container .inner-wrapper.message-priority .message-wrapper.message-wrapper {
    -webkit-transform: translate(0%, -50%);
    transform: translate(0%, -50%);
}
}
#view-toggle {
  transition: all 0.3s ease;
  border-radius: 50%;
  background-color: #29b474;
  color: #fff;
  display: none;
  height: 80px;
  width: 80px;
  position: fixed;
  z-index: 57;
  top: 50%;
  left: calc(100% - 40px);
  -webkit-transform: translate(0%, -50%);
  transform: translate(0%, -50%);
  text-align: left;
}
@media (max-width: 600px) {
#view-toggle {
    display: block;
}
}
#view-toggle .fa-icon {
  transition: all 0.3s ease;
  float: left;
  height: 80px;
  padding: 0 10px;
  width: 20px;
}
#view-toggle.message-priority {
  left: -40px;
  -webkit-transform: translate(0%, -50%);
  transform: translate(0%, -50%);
}


