
.pure-button {
  transition: all 0.3s ease;
  border-radius: 4px;
  background: none;
  background-color: transparent;
  border: #29b474 1px solid;
  color: #29b474;
}
.pure-button:hover {
  background: none;
  background-color: #29b474;
  color: #fff;
}
.pure-button.full-width {
  display: block;
  margin-bottom: 10px;
}
.pure-button.pure-button-primary {
  background-color: #29b474;
  color: #fff;
}
.pure-button.pure-button-primary:hover {
  background-color: #25a268;
}
.pure-button.pure-button-success {
  background-color: #29b474;
  color: #fff;
}
.pure-button.pure-button-success:hover {
  background-color: #25a268;
}
.pure-button.pure-button-subtle {
  background-color: transparent;
  border-color: #ccc;
  color: #666;
}
.pure-button.pure-button-subtle:hover {
  background-color: #e1e1e1;
  color: #444;
}
.pure-button.pure-button-text {
  border-color: transparent;
}
.pure-button.pure-button-homework {
  background-color: transparent;
  border-color: #fd3c51;
  color: #fd3c51;
}
.pure-button.pure-button-homework:hover {
  background-color: #fd3c51;
  color: #fff;
}
@import url("https://fonts.googleapis.com/css?family=Arimo");
@import url("https://fonts.googleapis.com/css?family=Source+Sans+Pro");
html,
body {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.pull-left {
  float: left;
}
.pull-right {
  float: right;
}
.clearfix {
  clear: both;
  float: none;
}
.fa-icon {
  width: auto;
  height: 1em;
}
.no-margin {
  margin: 0 !important;
}
.no-padding {
  padding: 0 !important;
}
.background-white {
  background-color: #fff !important;
}
.text-white {
  color: #fff;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.2s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.fade-enter-to,
.fade-leave {
  opacity: 1;
}
.main-container {
  border-radius: 4px;
  position: relative;
}
.main-container.main-container-padded {
  padding: 20px;
}
.content-block {
  border-radius: 4px;
  padding: 20px;
  margin: 20px 0 0 0;
}
.content-block.white-block {
  background-color: #fff;
}
.icon-margin {
  margin: 0 5px;
}
.message {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  max-height: 128px;
  margin: 10px;
  padding: 0 10px 40px 10px;
  padding-left: 50px;
  position: relative;
}
.message img.profile-image {
  border-radius: 50%;
  height: 40px;
  width: 40px;
  position: absolute;
  top: 0px;
  left: 0px;
}
.message p.message-content {
  margin: 0;
  padding: 0;
  color: #444;
  word-break: break-all;
}
.message p.message-content a,
.message p.message-content a:active {
  color: #444;
}
.message a.author-label {
  color: #46475d;
  font-weight: bold;
  text-decoration: none;
  font-size: 0.9em;
}
.message .message--footer {
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 36px;
  left: 42px;
  top: auto;
  overflow: hidden;
  position: absolute;
}
.message .message--footer ul.tweet-actions {
  margin: 0;
  padding: 0;
  list-style: none;
  background-color: #fff;
}
.message .message--footer ul.tweet-actions li {
  margin: 0;
  padding: 0;
  list-style: none;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  float: left;
  font-size: 1em;
  line-height: 36px;
  margin: 0 10px;
  max-width: 15%;
}
.message .message--footer ul.tweet-actions li a {
  color: #ccc;
  text-decoration: none;
}
.message .message--footer ul.tweet-actions li a:hover {
  color: #29b474;
}
.message .message--footer ul.tweet-actions li.message-timestamp {
  color: #ccc;
  font-size: 0.9em;
  float: right;
  max-width: 55%;
}


