
.pure-button {
  transition: all 0.3s ease;
  border-radius: 4px;
  background: none;
  background-color: transparent;
  border: #29b474 1px solid;
  color: #29b474;
}
.pure-button:hover {
  background: none;
  background-color: #29b474;
  color: #fff;
}
.pure-button.full-width {
  display: block;
  margin-bottom: 10px;
}
.pure-button.pure-button-primary {
  background-color: #29b474;
  color: #fff;
}
.pure-button.pure-button-primary:hover {
  background-color: #25a268;
}
.pure-button.pure-button-success {
  background-color: #29b474;
  color: #fff;
}
.pure-button.pure-button-success:hover {
  background-color: #25a268;
}
.pure-button.pure-button-subtle {
  background-color: transparent;
  border-color: #ccc;
  color: #666;
}
.pure-button.pure-button-subtle:hover {
  background-color: #e1e1e1;
  color: #444;
}
.pure-button.pure-button-text {
  border-color: transparent;
}
.pure-button.pure-button-homework {
  background-color: transparent;
  border-color: #fd3c51;
  color: #fd3c51;
}
.pure-button.pure-button-homework:hover {
  background-color: #fd3c51;
  color: #fff;
}
@import url("https://fonts.googleapis.com/css?family=Arimo");
@import url("https://fonts.googleapis.com/css?family=Source+Sans+Pro");
html,
body {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.pull-left {
  float: left;
}
.pull-right {
  float: right;
}
.clearfix {
  clear: both;
  float: none;
}
.fa-icon {
  width: auto;
  height: 1em;
}
.no-margin {
  margin: 0 !important;
}
.no-padding {
  padding: 0 !important;
}
.background-white {
  background-color: #fff !important;
}
.text-white {
  color: #fff;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.2s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.fade-enter-to,
.fade-leave {
  opacity: 1;
}
.main-container {
  border-radius: 4px;
  position: relative;
}
.main-container.main-container-padded {
  padding: 20px;
}
.content-block {
  border-radius: 4px;
  padding: 20px;
  margin: 20px 0 0 0;
}
.content-block.white-block {
  background-color: #fff;
}
.icon-margin {
  margin: 0 5px;
}
.rendered-markdown h1 {
  margin: 0;
  padding: 0;
  color: #444;
  margin-bottom: 5px;
}
.rendered-markdown h1,
.rendered-markdown h2,
.rendered-markdown h3,
.rendered-markdown h4,
.rendered-markdown h5,
.rendered-markdown p,
.rendered-markdown a,
.rendered-markdown li {
  color: #444;
}
.rendered-markdown h1:first-child,
.rendered-markdown h2:first-child,
.rendered-markdown h3:first-child,
.rendered-markdown h4:first-child,
.rendered-markdown h5:first-child,
.rendered-markdown p:first-child,
.rendered-markdown a:first-child,
.rendered-markdown li:first-child {
  margin-top: 0;
}
.rendered-markdown h1,
.rendered-markdown h2,
.rendered-markdown h3,
.rendered-markdown h4,
.rendered-markdown h5 {
  font-weight: 300;
}
.rendered-markdown p {
  margin: 10px 0;
}
.rendered-markdown a {
  color: #29b474 !important;
  text-decoration: underline;
}
.rendered-markdown a:hover {
  color: #29b474;
  cursor: pointer;
}
.rendered-markdown blockquote {
  border-left: #29b474 3px solid;
  color: #ccc;
  margin: 30px 20px 30px 0px;
  padding-left: 20px;
  font-style: italic;
}
.rendered-markdown img {
  display: inline-block;
  margin: 10px;
  width: 100%;
  max-width: 160px;
}
.rendered-markdown img[data-4c],
.rendered-markdown .fc-image img {
  display: block;
  margin: 0;
  max-width: 100%;
  padding: 0;
  width: 100%;
}
.rendered-markdown .md-bio {
  display: inline-block;
  min-height: 100px;
  overflow: hidden;
  padding: 10px;
  position: relative;
  width: 160px;
}
.rendered-markdown .md-bio img.md-bio--image {
  border-radius: 50%;
  margin: 0;
  padding: 0;
  margin: 0 20px;
  height: 120px;
  width: 120px;
}
.rendered-markdown .md-bio .md-bio--content {
  border-left: #e9e9e9 1px solid;
  padding: 10px;
}
.rendered-markdown .md-bio h5.md-bio--caption {
  margin: 0;
  padding: 0;
  font-size: 1em;
  font-weight: bold;
  height: 20px;
  line-height: 20px;
  margin: 5px 0;
  max-width: 160px;
  overflow: hidden;
  text-align: center;
}
.rendered-markdown .md-bio.md-bio--with-bio {
  border-radius: 6px;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  padding: 10px;
  padding-left: 100px;
  max-width: 100%;
  width: 100%;
}
.rendered-markdown .md-bio.md-bio--with-bio img.md-bio--image {
  margin: 0;
  padding: 0;
  position: absolute;
  top: 10px;
  left: 10px;
  height: 80px;
  width: 80px;
}
.rendered-markdown .md-bio.md-bio--with-bio h5.md-bio--caption {
  margin: 0;
  padding: 0;
  height: 20px;
  max-width: 100%;
  text-align: left;
  width: 100%;
}
.rendered-markdown .md-bio.md-bio--with-bio p.md-bio--bio {
  margin: 0;
  padding: 0;
  color: #666;
  text-align: left;
}


